<template>
    <div class="container">
        <div class="row mb-2">
            <div class="col-12">
                Donate to my Ethereum address&nbsp;&nbsp;
                <a :href="configuration.donate.urls.ethereum" target="_blank"
                    ><i class="h2 fab fa-ethereum"></i
                ></a>
            </div>
        </div>
        <div class="row mb-2">
            <div class="col-12 input-group">
                <input
                    type="text"
                    class="form-control"
                    readonly
                    v-model="
                        configuration.donate.cryptocurrency.ethereum.ethereum
                            .address
                    "
                    @click="copyEthereumAddressToClipboard"
                />
                <button
                    class="btn btn-primary"
                    type="button"
                    @click="copyEthereumAddressToClipboard"
                >
                    <i class="fas fa-copy"></i>
                </button>
            </div>
        </div>
        <div class="row mb-2">
            <div class="col-12 text-center">
                <img
                    src="data:image/png;base64,"
                    alt=""
                />
            </div>
        </div>
    </div>
</template>

<script>
import copy from "copy-to-clipboard";

// https://www.npmjs.com/package/toastr
import toastr from "toastr";

import Configuration from "@/configuration/default.js";

export default {
    data() {
        return {
            configuration: null,
        };
    },
    async created() {
        this.configuration = Configuration;
    },

    methods: {
        copyEthereumAddressToClipboard() {
            copy(Configuration.donate.cryptocurrency.ethereum.ethereum.address);

            toastr.options.showMethod = "slideDown";
            toastr.success("Copied");
        },
    },
};
</script>
